import React, { Component } from 'react';

export default class Currency extends Component {
    changeCurrency = currency => {
        const { context } = this.props;
        context.changeCurrency(currency);
    };

    render() {
        return (
            <div className="container">
                <h1>Тестовое задание кошелёк.ру</h1>
                <p>Ким Сергей. Телеграм @srgykim</p>
                <br />
                <div className="dropdown">
                <button className="btn btn-secondary dropdown-toggle" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    Символы
                </button>
                <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
                    <a className="dropdown-item" href="#" onClick={() => this.changeCurrency("BTCUSDT")}>BTCUSDT</a>
                    <a className="dropdown-item" href="#" onClick={() => this.changeCurrency("BNBBTC")}>BNBBTC</a>
                    <a className="dropdown-item" href="#" onClick={() => this.changeCurrency("ETHBTC")}>ETHBTC</a>
                </div>
                </div>
            </div>
        );
    }
};
